.main-header {
  position: relative;
  width: 100%;
  z-index: 2;
  background-color: transparent;
}

.main-header__about {
  position: fixed;
  min-height: 120px;
  background-image:  linear-gradient(218.77deg, rgba(110, 59, 38, 0.85) 13.28%, rgba(107, 98, 96, 0.85) 85.15%),
    url("../img/bg-port-desktop@1x.jpg");
  // background-image: url("../img/bg-port-desktop@1x.jpg");
  background-position-y: top;
  background-size: 100%;
}

.main-header__about::before {
  content: "";
  position: absolute;
  height: 50px;
  width: 100%;
  top: 120px;
  background: linear-gradient(180deg, rgba(86, 70, 63, 0.8) 0%, rgba(196, 196, 196, 0.18) 58.85%, rgba(196, 196, 196, 0) 100%);
  // background: linear-gradient(180deg, #c4c4c4 0%, #c4c4c4 58.85%, rgba(196, 196, 196, 0) 100%);
  // box-shadow: 0 4px 4px rgba(0, 0, 0, 0.25), 0 4px 4px rgba(0, 0, 0, 0.25);
  // transform: matrix(1, 0, 0, -1, 0, 0);
}
